.modal {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 42.85rem;
  max-width: 100%;
  height: 30rem;
  max-height: 100%;
  z-index: 100;
  background: #525252;
  box-shadow: 0 0 60px 10px rgba(0, 0, 0, 0.9); }
  .modal .close-button {
    position: absolute;
    z-index: 1;
    top: 10px;
    right: 20px;
    border: 0;
    background: black;
    color: white;
    padding: 5px 10px;
    font-size: 1.3rem; }
  .modal .modal-guts {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 20px 50px 20px 20px;
    display: flex;
    flex-flow: row wrap; }
