body {
  display: flex;
  flex-flow: column wrap; }
  body .introdaction {
    display: flex;
    font-family: "Courier New", Courier, monospace;
    justify-content: center; }
    body .introdaction .main_text {
      width: 45rem;
      font-size: 3rem;
      color: #000080; }
      body .introdaction .main_text a {
        text-decoration: none;
        color: yellow; }
    body .introdaction .main_screenshoot {
      margin: 3rem;
      width: 1000px;
      box-shadow: 0 0 60px 10px rgba(16, 47, 221, 0.466); }
  body .examples_of_animations {
    display: flex;
    flex-flow: column wrap; }
    body .examples_of_animations h2 {
      font-size: 4rem; }
    body .examples_of_animations .exapmle_cont {
      display: flex;
      flex-flow: row wrap;
      justify-content: center; }
      body .examples_of_animations .exapmle_cont img {
        margin-right: 2rem; }
  body .functionality_overview {
